<template>
  <va-card
    class="card"
    :color="computedWidgetColor"
    :title="$t('domains.panel-title')"
  >
    <va-inner-loading :loading="loading">
      <div
        :style="{
          opacity: this.loading ? 0.2 : 1,
        }"
      >
        The domains you own on
        <a
          href="https://ap.www.namecheap.com/domains/list/"
          :style="{ color: $themes.primary }"
          target="_blank"
          >Namecheap</a
        >
        are listed here.
        <br />
        <br />

        <div class="domains-title">
          <div class="domains-title_prop">Name</div>
          <div class="domains-title_prop">Assigned to</div>
          <div class="domains-title_prop">Lifetime</div>
          <div class="domains-title_prop">Issue</div>
        </div>
        <div class="domains-table">
          <div
            class="domains-table_item"
            v-for="domain in domains"
            :key="domain.ID"
          >
            <div class="domains-table_item_prop center domain">
              <span
                ><span class="suffix">https://www.</span>
                {{ domain.Name }}</span
              >
            </div>

            <div class="domains-table_item_prop">
              <va-select
                :options="managers"
                :value="domain.Assigned"
                class="ma-0"
                no-clear
              />
            </div>
            <div class="domains-table_item_prop center">
              {{ getLifetime(domain) }}
            </div>
            <div
              class="domains-table_item_prop center"
              :class="{ clickable: !!checkIssue(domain) }"
            >
              <va-popover
                v-if="checkIssue(domain)"
                :icon="checkIssue(domain).icon"
                :color="checkIssue(domain).color"
                :title="checkIssue(domain).title"
                :message="checkIssue(domain).message"
                placement="right"
              >
                <span class="full-sized">
                  <va-icon name="fa fa-times" color="danger" />
                  {{ ' In trouble' }}
                </span>
              </va-popover>
              <div v-else>
                <va-icon name="fa fa-check" color="success" /> {{ 'Good' }}
              </div>
            </div>
          </div>
        </div>
      </div>
    </va-inner-loading>
  </va-card>
</template>

<script>
import moment from 'moment'

import * as Domains from '@/api/Domains'

export default {
  name: 'domains-panel',
  data: () => ({
    loading: false,
    popover: {
      title: 'Hey folks!',
      message: 'This tooltip is amazing:D',
      icon: {
        icon: 'fa fa-print',
        text: 'print',
      },
      color: 'warning',
    },
    managers: [
      'Nobody',
      'joker211@gmail.com',
      'elepahnt@gmail.com',
      'cqqkkmmnnqqqi@ljsingh.com',
      'hauken.lopo@gmail.com',
      'fraud.num.one@gmail.com',
      '4yassinos1111@kittiza.com',
      'umq04klzni@gmail.com',
      'maz.pum21@gmail.com',
      'znile111@gmail.com',
      'flancefran@86sihu.com',
    ],
    domains: [
      {
        id: 'm0912_mzpumq04klznilepj',
        name: 'cryptobit.max',
        created: '10.02.2021',
        expires: '10.02.2022',
        verified: true,
        locked: false,
        assigned: 'joker211@gmail.com',
      },
      {
        id: 'm0912_mzpum1q04klznilepj',
        name: 'exmax.min',
        created: '02.02.2021',
        expires: '02.02.2022',
        verified: false,
        locked: false,
        assigned: 'elepahnt@gmail.com',
      },
      {
        id: 'm01_1mzpumq04klznilepj',
        name: 'cryptobit.max',
        created: '10.02.2021',
        expires: '10.02.2022',
        verified: true,
        locked: false,
        assigned: 'Nobody',
      },
      {
        id: 'm0912_mzpumq04klznil1epj',
        name: 'bitmax.corp',
        created: '13.02.2021',
        expires: '15.02.2021',
        verified: true,
        locked: false,
        assigned: 'fraud.num.one@gmail.com',
      },
      {
        id: 'm0912_m1zpumq04klznilepj',
        name: 'cryptobit.max',
        created: '10.02.2021',
        expires: '10.02.2022',
        verified: true,
        locked: false,
        assigned: 'Nobody',
      },
      {
        id: 'm0912_mzpumq1204klznilepj',
        name: 'crypto.ex',
        created: '22.04.2021',
        expires: '22.04.2032',
        verified: true,
        locked: false,
        assigned: 'maz.pum21@gmail.com',
      },
      {
        id: 'm0912_mzp4umq04klznilepj',
        name: 'bitrade.io',
        created: '10.02.2021',
        expires: '10.02.2022',
        verified: true,
        locked: true,
        assigned: 'znile111@gmail.com',
      },
    ],
  }),
  computed: {
    computedWidgetColor() {
      return this.$themes.background
    },
  },
  methods: {
    checkIssue(domain) {
      if (domain.IsLocked) {
        return {
          title: 'Validation',
          icon: 'fa fa-lock',
          color: 'danger',
          message: 'This domain is locked.',
        }
      } else if (!domain.IsExpired) {
        return {
          title: 'Validation',
          icon: 'fa fa-calendar-times-o',
          color: 'danger',
          message: 'The domain was expired. Renew it in Namecheap.',
        }
      } else {
        return null
      }
    },
    getLifetime(d) {
      var startDate = moment()
      var endDate = moment(d.Expires, 'MM/DD/YYYY')

      var years = endDate.diff(startDate, 'years')
      var months = endDate.diff(startDate, 'months')
      var days = endDate.diff(startDate, 'days')
      var hours = endDate.diff(startDate, 'hours')
      var minutes = endDate.diff(startDate, 'minutes')
      var seconds = endDate.diff(startDate, 'seconds')

      if (years) {
        return years + ' years'
      } else if (months) {
        return months + ' months'
      } else if (days) {
        return days + ' days'
      } else if (hours) {
        return hours + ' hours'
      } else if (minutes) {
        return minutes + ' minutes'
      } else {
        return seconds + ' seconds'
      }
    },
  },
  async created() {
    this.domains = (
      await Domains.getList()
    ).data.response[0].DomainGetListResult[0].Domain.map(d => d.$)
  },
}
</script>

<style lang="scss">
.card {
  box-shadow: none;
  border-radius: 0.375rem;
}

.full-sized {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.suffix {
  opacity: 0.1;
  width: 0px;
  transition: all 0.5s ease;
}

.domains-table {
  display: flex;
  flex-direction: column;
  font-family: Montserrat;

  &_item {
    display: grid;
    height: 35px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    background-color: #00000011;
    border-bottom: 1px solid #00000042;

    &_prop {
      width: 100%;
      height: 100%;
      text-align: center;
      overflow: hidden;
      position: relative;
    }

    &_prop.center {
      display: grid;
      align-content: center;
    }

    &_prop.clickable {
      cursor: pointer;
      position: relative;

      &:hover {
        background-color: #ff000022;
      }
    }

    &_prop.domain {
      cursor: pointer;
      text-align: left;
      text-indent: 1rem;
    }

    &_prop.domain:hover {
      background-color: #00000055;
      .suffix {
        opacity: 1;
        font-size: 1rem;
      }
    }

    &_prop:nth-child(odd) {
      background-color: #00000011;
    }
  }
}

.domains-title {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;

  &_prop {
    width: 100%;
    height: 100%;
    display: grid;
    text-align: center;
    align-content: center;
    border: 1px solid #00000066;
    border-bottom: 3px solid #00000066;
    height: 30px;
    font-size: 1.2rem;
    background-color: #00000011;
  }
}
</style>
